.toplevel_page_ditty {
  background: #fff;
}
.toplevel_page_ditty #wpcontent {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.toplevel_page_ditty #wpbody {
  flex: 1;
}
.toplevel_page_ditty #wpbody-content {
  padding: 0;
  float: none;
}

.ditty-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  color: #23282d;
  background: rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 3px;
  padding: 10px 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s ease, background-color 0.25s ease;
}

#ditty-page {
  padding-right: 350px;
  box-sizing: border-box;
}
#ditty-page * {
  box-sizing: border-box;
}
#ditty-page__header {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #ccc;
}
#ditty-page__header h2 {
  margin: 0;
}
#ditty-page__content {
  display: flex;
  flex-wrap: nowrap;
}

#ditty-preview {
  flex: 1;
}

#ditty-editor {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 350px;
  background: #f7f7f7;
  border-left: 1px solid #ccc;
  box-sizing: border-box;
}
#ditty-editor * {
  box-sizing: border-box;
}
.wp-admin #ditty-editor {
  top: 32px;
}

.ditty-editor__contents {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.ditty-editor__tabs {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  gap: 1px;
  height: 40px;
  font-size: 14px;
  background: #e8e9eb;
}
.ditty-editor__tabs--primary {
  height: 60px;
  background: #32373c;
}
.ditty-editor__tabs--primary .ditty-editor__tab {
  color: rgba(255, 255, 255, 0.8);
  background: #32373c;
}
.ditty-editor__tabs--primary .ditty-editor__tab:hover:not(.ditty-editor__tab--active) {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
}
.ditty-editor__tabs--primary .ditty-editor__tab--active {
  color: #fff;
  background-color: #19bf7c;
}
.ditty-editor__tabs--secondary {
  border-bottom: 1px solid #ccc;
}
.ditty-editor__tab {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 62px;
  font-weight: 600;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  color: #616970;
  background: #f2f3f5;
  border-radius: 0;
  border: none;
  cursor: pointer;
  transition: color 0.25s ease, background-color 0.25s ease;
}
.ditty-editor__tab:hover:not(.ditty-editor__tab--active) {
  color: #fff;
  background-color: #19bf7c;
}
.ditty-editor__tab--active {
  width: auto;
  color: #19bf7c;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #fff;
}
.ditty-editor__tab__icon {
  flex: 0 0 auto;
  display: block;
  font-size: 18px;
}
.ditty-editor__tab__label {
  flex: 1;
  display: block;
}
.ditty-editor__panels {
  flex: 1;
  overflow: hidden;
}
.ditty-editor__panels > .ditty-editor__panel {
  height: 100%;
}
.ditty-editor__panels > .ditty-editor__panel > .ditty-editor__panel__content {
  padding: 10px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.ditty-editor__panel {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.ditty-editor__panel__header {
  flex: 0 0 auto;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}
.ditty-editor__panel__content {
  flex: 1;
}

.ditty-editor-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.05);
  padding: 10px;
  margin: 0 0 5px;
  border-radius: 3px;
  overflow: hidden;
  transition: background-color 0.25s ease, border-color 0.25s ease;
}
.ditty-editor-item__icon {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #ccc;
  margin-right: 10px;
}
.ditty-editor-item__icon i,
.ditty-editor-item__icon svg {
  display: block;
  font-size: 15px;
  text-align: center;
  line-height: 40px;
}
.ditty-editor-item__label {
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ditty-editor-item__settings {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin-left: 10px;
  cursor: pointer;
}
.ditty-editor-item__settings:hover {
  background: orange;
}
.ditty-editor-item__settings i {
  display: block;
  font-size: 20px;
  text-align: center;
  line-height: 40px;
}

.ditty-list--sortable .ditty-editor-item {
  cursor: grab;
}
.ditty-list--sortable .ditty-editor-item:hover:active {
  cursor: grabbing;
}

.ditty-editor__panel--itemTypes .ditty-editor-item__content h3 {
  margin: 0;
}
.ditty-editor__panel--itemTypes .ditty-editor-item__content p {
  margin: 0;
}